/* ==========================================================================
   Header Vertical navigation style - begin
   ========================================================================== */

.eltdf-header-vertical {

    .eltdf-vertical-alignment-center {
	    
        .eltdf-vertical-menu-outer {
	        position: absolute;
	        top: 0;
	        left: 0;
	        height: 100%;
	        width: 100%;
	        display: table;
	        padding: 100px 0 150px;
        }

        .eltdf-vertical-menu {
            display: table-cell;
            height: auto;
            min-height: 0;
            vertical-align: middle;
        }
    }
    
    .eltdf-vertical-menu-outer {
        @include eltdfRelativeHolderLayout();
	    box-sizing: border-box;
    }

    .eltdf-vertical-menu {
        @include eltdfRelativeHolderLayout();
        margin: 75px 0 30px;
        padding: 0;
        z-index: 100;
        
        ul {
            position: relative;
            list-style-type: none;
            padding: 0;
            margin: 0;
            
            li {
                position: relative;
                padding: 0;
                margin: 0;
                
                a {
	                position: relative;
	                display: block;
	                margin: 0;
	                padding: 5px 0;
                    color: #8c8c8c;
                    
                    &:hover {
                        color: $first-main-color;

                        .item_outer .item_text:before {
                            height: 2px;
                        }
                    }
                    
                    .item_outer {
                        position: relative;
                        display: block;
                        padding: 0 15px 0 0;
                        box-sizing: border-box;

                        .item_text {
                            display: inline-block;
                            position: relative;

                            &:before {
                                content: '';
                                position: absolute;
                                top: 26px;
                                left: -1px;
                                height: 0;
                                width: calc(100% + 2px);
                                @include eltdfTransition(all ease 0.25s);
                                background: $first-main-color;
                            }
                        }
                    }
                    
                    .eltdf-menu-arrow {
                        position: absolute;
                        top: 1px;
                        right: 0;
	                    font-size: inherit;
	                    line-height: inherit;
                        display: none;
                        margin: 0;
                        @include eltdfTransition(transform .2s ease-in-out);
                        @include eltdfTransform(rotate(-90deg));
                    }
                    
                    .eltdf-menu-featured-icon {
                        position: relative;
                        top: -4px;
                        left: 3px;
                        color: $default-heading-color;
                        font-size: 12px;
                        margin: 0;
                    }
                    
                    .menu_icon:not(.blank):not(.null) {
                        padding: 0 5px 0 0;
                    }
                }
                
                ul {
                    padding: 3px 0;
                    
                    li {
                        
                        &.sub {
                            
                            > a {
                                
                                .item_outer {
                                    position: relative;
                                    display: block;
                                }
                            }
                            
                            &.current_page_item > a,
                            &.current-menu-ancestor > a {
                            }
                        }
                    }
                }
                
                .second {
                    display: none;
                    @include eltdfTransform(translateZ(0));
                    
                    .inner {
                        position: relative;
                        display: block;
                        padding: 0;
                        z-index: 997;
                    }
                }
            }
        }
        
        > ul {
            
            > li {
                
                > a {
                    font-size: 14px;
                    line-height: 32px;
                    font-weight: 600;
                    color: $default-heading-color;
                    letter-spacing: .1em;
                    text-transform: uppercase;
                    
                    span.item_text {
	                    position: relative;
	                    display: inline-block;
	                    vertical-align: top;
                    }

                    &:hover {
                        color: $first-main-color;
                    }
                }

                &.eltdf-active-item > a,
                &.current_page_item > a,
                &.current-menu-item > a,
                &.current-menu-ancestor > a {
                    color: $first-main-color;
                    span.item_text:before {
                        height: 2px;
                    }
                }
            }
        }
    }
}

.eltdf-light-header.eltdf-header-vertical .eltdf-vertical-menu ul li {
    
    a {
        color: $header-light-color !important;
        
        &:hover {
            color: $header-light-color !important;
        }

        .item_outer .item_text:before {
            background: $header-light-color;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-light-color !important;
        }
    }
}

.eltdf-light-header.eltdf-header-vertical .eltdf-vertical-menu > ul > li {
    
    &.eltdf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-light-color !important;
    }
}

.eltdf-dark-header.eltdf-header-vertical .eltdf-vertical-menu ul li {
    
    a {
        color: $header-dark-color !important;
        
        &:hover {
            color: $header-dark-color !important;
        }

        .item_outer .item_text:before {
            background: $header-dark-color;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-dark-color !important;
        }
    }
}

.eltdf-dark-header.eltdf-header-vertical .eltdf-vertical-menu > ul > li {
    
    &.eltdf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-dark-color !important;
    }
}
/* ==========================================================================
   Header Vertical navigation style - end
   ========================================================================== */