/* ==========================================================================
   Header main menu style - begin
   ========================================================================== */

.eltdf-main-menu {
    @include eltdfRelativeHolderLayout();
    height: 100%;
    width: auto;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {
                font-size: 16px;
                line-height: 24px;
                color: #adadad;

                &:hover {
                    color: $first-main-color;
                }

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .eltdf-menu-featured-icon {
                    position: relative;
                    top: -4px;
                    left: 5px;
                    color: currentColor;
                    font-size: 10px;
                    margin: 0;

                    &:before {
                        margin: 0;
                    }
                }
            }
        }
    }

    > ul {
        @include eltdfRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include eltdfRelativeHolderLayout();
                height: 100%;
                padding: 0 18px;
                color: $default-heading-color;
                font-size: 14px;
                line-height: 23px;
                box-sizing: border-box;
                letter-spacing: .12em;
                text-transform: uppercase;
                font-weight: 500;

                > span.item_outer {
                    position: relative;
                    display: block;
                    top: 50%;
                    @include eltdfTransform(translateY(-50%));

                    .item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        padding-top: 1px;

                        &:before {
                            content: '';
                            position: absolute;
                            top: 25px;
                            left: -2px;
                            height: 0;
                            width: calc(100% + 2px);
                            @include eltdfTransition(all 0.15s ease);
                            background: $first-main-color;
                        }
                    }
                }

                &:hover {
                    color: $first-main-color;
                    > span.item_outer .item_text:before {
                        height: .16em;
                    }
                }
            }

            &.eltdf-active-item > a {
                color: $first-main-color;

                > span.item_outer .item_text:before {
                    height: .16em;
                }
            }

            &.has_sub {
                > a .eltdf-menu-arrow {
                    display: none;
                }
            }
        }
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-color;
            }

            .item_outer .item_text:before {
                background: $header-light-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-light-color;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $header-dark-color;
            }

            .item_outer .item_text:before {
                background: $header-dark-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-dark-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */
.eltdf-drop-down {

    .second {
        position: absolute;
        display: block;
        left: -12px;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;
        @include eltdfTransition(top 0.15s ease-out);

        &.eltdf-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include eltdfTransition(opacity 0.2s ease-out);
        }

        &.right {
            right: -12px;
            left: auto;
        }
    
        .inner {
            position: relative;
            display: block;
            padding: 0;
            margin: 0;
            z-index: 997;
    
            ul {
        
                li {
                    display: block;
                    float: none;
                    height: auto;
            
                    .widget {
                        width: 100%;
                        margin: 0 !important;
                    }
            
                    a {
                        display: block;
                        float: none;
                        margin: 0;
                        padding: 8px 30px;
                
                        .item_outer {
                            position: relative;
                            display: block;
                            box-sizing: border-box;

                            .item_text {
                                position: relative;
                                display: inline-block;
                                vertical-align: top;
                                padding-top: 1px;

                                &:before {
                                    content: '';
                                    position: absolute;
                                    top: 25px;
                                    left: -1px;
                                    height: 0;
                                    width: calc(100% + 2px);
                                    @include eltdfTransition(all 0.2s ease);
                                    background: $first-main-color;
                                }
                            }
                        }
                
                        &:hover {
                            span.item_outer .item_text:before {
                                height: 2px;
                            }
                        }
                    }
            
                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                    }
            
                    &.sub {
                
                        > a {
                    
                            .item_outer {
                        
                                &:after {
                                    position: absolute;
                                    display: inline-block;
                                    vertical-align: middle;
                                    top: -2px;
                                    right: -2px;
                                    content: '\e04b';
                                    font-family: 'linea-arrows-10';
                                    font-size: 17px;
                                    font-weight: normal;
                                    font-style: normal;
                                    text-rendering: auto;
                                    -webkit-font-smoothing: antialiased;
                                    -moz-osx-font-smoothing: grayscale;
                                }
                            }
                        }
                    }
                }
        
                &.right {
            
                    li {
                
                        a {

                            .item_text {
                                position: relative;
                                display: inline-block;
                                vertical-align: top;
                                padding-top: 1px;

                                &:before {
                                    content: '';
                                    position: absolute;
                                    top: 25px;
                                    left: -1px;
                                    height: 0;
                                    width: calc(100% + 2px);
                                    @include eltdfTransition(all 0.2s ease);
                                    background: $first-main-color;
                                }
                            }

                            &:hover {
                                > span.item_outer .item_text:before {
                                    height: 2px;
                                }
                            }
                        }
                
                        &.current-menu-ancestor > a,
                        &.current-menu-item > a {
                        }
                
                        &.sub {
                    
                            > a {
                                .item_outer {
                            
                                    &:after {
                                        right: auto;
                                        left: -2px;
                                        content: "\e03f";
                                    }
                                }
                            }
                        }
                
                        a {
                            text-align: right;
                        }
                
                        ul {
                            left: -100%;
                        }
                    }
                }
            }
        }
    }

    li {
        
        &.left_position {
            
            .second {
                left: 0;
            }
        }
        
        &.right_position {
            
            .second {
                left: auto;
                right: 0;
            }
        }
    }

    > ul > li {
        &:hover > .second {
            z-index: 20;
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: $default-heading-color;
            padding: 19px 0;

            li {
                width: 230px;

                ul {
                    position: absolute;
                    left: 100%;
                    top: -19px;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;
                    -webkit-transform: translateZ(0);
                    @include eltdfTransition(opacity .4s ease-out);
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.eltdf-drop-down .wide {

    .second {

        .inner {
            background-color: $default-heading-color;
            text-align: center;

            ul {

                li {

                    a {
                        padding: 8px 0;
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                width: 1100px;
                padding: 35px 0;
                box-sizing: border-box;

                @media only screen and (min-width: 1400px) {
                    .eltdf-grid-1300 & {
                        width: $grid-width-1300;
                    }
                }

                @media only screen and (min-width: 1300px) {
                    .eltdf-grid-1200 & {
                        width: $grid-width-1200;
                    }
                }

                @media only screen and (min-width: 1200px) {
                    .eltdf-grid-1000 & {
                        width: $grid-width-1000;
                    }
                }

                @media only screen and (min-width: 1024px) {
                    .eltdf-grid-800 & {
                        width: $grid-width-800;
                    }
                }

                > li {
                    float: left;
                    width: 25%;
                    margin: 0;
                    padding: 0 26px;
                    border-left: 1px solid #353535;
                    box-sizing: border-box;

                    &:nth-child(4n+1) {
                        clear: both;
                    }

                    > a {
                        padding: 0 0 13px;
                        color: #fff;
                        font-size: 14px;
                        font-weight: 600;
                        text-transform: uppercase;
                        line-height: 1.1em;
                        letter-spacing: .1em;

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:after {
                                display: none;
                            }

                            .item_text {
                                &:before {
                                    display: none;
                                }
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                    }
                }
            }
        }
    }

    &.left_position,
    &.right_position {

        .second .inner > ul {
            width: auto;
            white-space: nowrap;

            > li {
                float: none;
                display: inline-block;
                vertical-align: top;

                &:nth-child(4n+1) {
                    border-left: none;
                }
            }
        }

        .second ul li {
            width: 200px;
        }
    }
}
/* ==========================================================================
   Header main menu style - end
   ========================================================================== */