/* ==========================================================================
   Header Vertical - Closed style - begin
   ========================================================================== */
$vertical-menu-width: 352px;
$closed-vertical-menu-width: 70px;

@import 'blocks/header-vertical-closed-navigation.scss';
@import 'blocks/header-vertical-closed-widgets.scss';

.eltdf-header-vertical-closed {
	
    .eltdf-content,
    footer{
        padding-left: $closed-vertical-menu-width;
        box-sizing: border-box;
    }

	.eltdf-vertical-menu-area {
        position: fixed;
		top: 0;
		left: calc(#{$closed-vertical-menu-width} - #{$vertical-menu-width});
		height: 100%;
		width: $vertical-menu-width;
		padding: 50px 0 50px 70px;
		text-align: center;
		background-color: #fff;
	    box-sizing: border-box;
		outline: none;
		z-index: 101;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
		@include eltdfTransition(left .5s cubic-bezier(.77,0,.175,1));
		
		&.eltdf-with-scroll {
			overflow: hidden;
		}

		.eltdf-vertical-area-opener {
			position: fixed;
			left: 0;
			top: 0;
			width: $closed-vertical-menu-width;
			border-right: 1px solid #ebebeb;
			background-color: #fff;
			height: 100%;
			z-index: 1000;

			.eltdf-vertical-area-opener-line {
				position: absolute;
				top: 50%;
				left: 50%;
				display: block;
				width: 23px;
				height: 1px;
				margin-left: -10px;
				background-color: #212121;
				@include eltdfTransition(transform 0.3s ease);

				&.eltdf-line-1 {
					@include eltdfTransform(translateY(-6px));
				}

				&.eltdf-line-3 {
					@include eltdfTransform(translateY(6px));
				}
			}

			&:hover {
				.eltdf-vertical-area-opener-line {
					&.eltdf-line-1,
					&.eltdf-line-2,
					&.eltdf-line-3 {
						@include eltdfTransform(rotate(45deg));
					}
				}
			}
		}

		.eltdf-vertical-menu {
			opacity: 0;
			@include eltdfTransition(opacity .15s ease-out);
		}

	    &.active {
		    left: 0;
		    
		    .eltdf-vertical-area-opener {
			    
			    .eltdf-vertical-area-opener-line {
					&.eltdf-line-1 {
						display: none;
					}

					&.eltdf-line-2 {
						top: 50%;
						@include eltdfTransform(rotate(45deg));
					}

					&.eltdf-line-3 {
						top: 50%;
						@include eltdfTransform(rotate(-45deg));
					}
			    }
		    }

		    .eltdf-vertical-menu {
			    opacity: 1;
		    }
	    }
    }

    .eltdf-vertical-menu-area-inner {
        @include eltdfRelativeHolderLayout();
        height: 100%;

		.eltdf-vertical-menu-outer {
			position: relative;
			top: 50%;
			@include eltdfTransform(translateY(-50%))
		}
    }

    &.admin-bar .eltdf-vertical-menu-area {
        top: 32px;
    }
	
	.eltdf-logo-wrapper {
		@include eltdfRelativeHolderLayout();
		z-index: 100;
		display: none;
		
		a {
			display: block;
			position: relative;
			height: 100%;
		}
		
		img {
			display: block;
			margin: 0 auto;
			height: auto;
			max-height: 100%;
			@include eltdfTransition(opacity 0.3s ease);
		}
	}



    .eltdf-vertical-area-background {
        position: fixed;
		backface-visibility: hidden;
	    top: 0;
	    left: calc(#{$closed-vertical-menu-width} - #{$vertical-menu-width});
	    width: $vertical-menu-width;
	    height: 100%;
	    background-position: right top;
	    background-repeat: no-repeat;
	    background-color: #fff;
		border-right: 1px solid #ebebeb;
	    background-size: cover;
        z-index: 0;
        opacity: 1;
        @include eltdfTransition(opacity 0.5s ease, background-color 0.5s ease, left .5s cubic-bezier(.77,0,.175,1));
    }

	.active .eltdf-vertical-area-background {
		left: 0;
	}

	.eltdf-vertical-area-bottom-logo {
		position: fixed;
		top: 0;
		left:0;
		width: $closed-vertical-menu-width;
		text-align: center;
		z-index: 1000;
		@include eltdfTransition(left .15s ease-in-out);

		.eltdf-vertical-area-bottom-logo-inner {
			position: absolute;
			top: 0;
			width: 100%;
			text-align: center;
			box-sizing: border-box;

			a {
				display: block;
				padding: 25px;
			}
		}
	}
}
/* ==========================================================================
   Header Vertical - Closed style - end
   ========================================================================== */