/* ==========================================================================
   Header Vertical - Closed widget style - begin
   ========================================================================== */

.eltdf-header-vertical-closed {

    .eltdf-vertical-area-widget-holder {
        @include eltdfRelativeHolderLayout();
        margin: 10px 0 0;
        z-index: 100;
    }

    .eltdf-vertical-alignment-center{

        .eltdf-vertical-area-widget-holder {
            position: absolute;
	        bottom: 0;
	        left: 0;
	        width: 100%;
        }
    }
}
/* ==========================================================================
   Header Vertical - Closed widget style - end
   ========================================================================== */