.eltdf-header-vertical {

    .eltdf-vertical-area-widget-holder {
        position: absolute;
        bottom: 30px;
        left: 0;
        display: inline-block;
        width: 100%;
        vertical-align: middle;
        margin: 10px 0 0;
        z-index: 100;
    }

    .eltdf-vertical-alignment-center{

        .eltdf-vertical-area-widget-holder {
            position: absolute;
            width: 100%;
            bottom: 0px;
            left: 0px;
        }
    }
}